import Joi from "joi";
import generateData from "../dataSource/generateData";
import { Data } from "../types/data";
import { validateDateAtWeek } from "./validateDate";

const { object, string, number, array, boolean, date } = Joi.types();
//???:fixme:requiredrequired
const
    Time_ = array.length(2).ordered(number.min(0).max(23).required(), number.min(0).max(59).required()).required(),
    TimeBlock_ = object.keys({
        startTime: Time_.required(),
        endTime: Time_.required(),
    }).required(),
    /**`TimeBlock[]`*/
    TimeBlock_$$_ = array.items(TimeBlock_).required(),
    Lesson_ = object.keys({
        //todo:Lesson
    }).required(),
    LessonsInADay_ = array.items(Lesson_).custom((value, helper)=>{
        //todo: `Data.lessonTemplates.morning.length + Data.lessonTemplates.afternoon.length + Data.lessonTemplates.night.length` 
    }).required(),
    Data_ = object.keys({
        timestamp: number.required(),
        config: object.keys({
            startWeek: date.custom((value :unknown, helpers)=>{
                //todo:DateAtWeek
                if(value instanceof Date){
                    
                    return value as DateAtWeek;
                }
                return helpers.error("date.invalid");
            }).required(),
            weeksInTerm: number.required(),
            showWeekend: boolean.required(),
            startWeekAtSunday: boolean.required(),
        }).required(),
        ini_state: object.keys({
            currentWeek: number.required(),
        }).required(),
        lessonTemplates: object.keys({
            morning: TimeBlock_$$_.required(),
            afternoon: TimeBlock_$$_.required(),
            night: TimeBlock_$$_.required(),
        }).required(),
        lessons: array.length(7).items(LessonsInADay_).required()
    }).required();


export default function validateData(input :string | null) :Data{
    if(input === null) return generateData();
    let gotData;
    try{ gotData = JSON.parse(input); }
    catch(e :unknown){
        //todo:
        console.error("JSON", input, e);
        //fallbackJSON
        gotData = generateData();
    }
    return {} as Data;
}